import React from 'react';
import { Link } from 'react-router-dom';
import './ARFeature.css';

const ARFeature = () => {
    return (
        <section className="ar-feature-section">
            <div className="container">
                <div className="ar-feature-content">
                    <div className="ar-feature-text">
                        <h2> Tri nghim mi: Xem sn phm bng AR/VR</h2>
                        <p>
                            Khm ph cng ngh thc t tng cng (AR) v thc t o (VR) 
                             xem chi tit sn phm mt cch sng ng v chn thc nht.
                        </p>
                        <div className="ar-features-list">
                            <div className="feature-item">
                                <span className="feature-icon"></span>
                                <div>
                                    <strong>AR Mode:</strong> Xem sn phm trong th gii tht
                                </div>
                            </div>
                            <div className="feature-item">
                                <span className="feature-icon"></span>
                                <div>
                                    <strong>VR Mode:</strong> Khm ph trong mi trng 3D
                                </div>
                            </div>
                            <div className="feature-item">
                                <span className="feature-icon"></span>
                                <div>
                                    <strong>3 View Modes:</strong> Bn ngoi, bn trong, tch ri
                                </div>
                            </div>
                        </div>
                        <Link to="/ar-experience" className="ar-cta-button">
                            Tri nghim ngay
                        </Link>
                    </div>
                    <div className="ar-feature-visual">
                        <div className="ar-demo-card">
                            <div className="demo-phone">
                                <div className="phone-screen">
                                    <div className="ar-overlay">
                                        <div className="ar-object"></div>
                                        <div className="ar-particles">
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="demo-text">
                                <p>Hng camera vo marker  xem magic! </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default ARFeature;
