import React, { useState } from 'react';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import './CareersPage.css';

function CareersPage() {
    const [selectedDepartment, setSelectedDepartment] = useState('all');

    const departments = [
        { id: 'all', name: 'Tt C' },
        { id: 'tech', name: 'Cng Ngh' },
        { id: 'sales', name: 'Bn Hng' },
        { id: 'marketing', name: 'Marketing' },
        { id: 'hr', name: 'Nhn S' },
        { id: 'finance', name: 'Ti Chnh' }
    ];

    const jobOpenings = [
        {
            id: 1,
            title: 'Senior Frontend Developer',
            department: 'tech',
            location: 'H Ch Minh',
            type: 'Full-time',
            experience: '3-5 nm',
            salary: '15-25 triu',
            description: 'Pht trin giao din ngi dng cho cc ng dng web v mobile.',
            requirements: [
                'Thnh tho React, Vue.js hoc Angular',
                'Kinh nghim vi TypeScript',
                'Hiu bit v responsive design',
                'K nng lm vic nhm tt'
            ],
            posted: '2 ngy trc',
            urgent: true
        },
        {
            id: 2,
            title: 'Sales Manager',
            department: 'sales',
            location: 'H Ni',
            type: 'Full-time',
            experience: '5-7 nm',
            salary: '20-30 triu',
            description: 'Qun l i ng bn hng v pht trin chin lc kinh doanh.',
            requirements: [
                'Kinh nghim qun l i ng bn hng',
                'K nng giao tip xut sc',
                'Hiu bit v th trng in thoi',
                'Kh nng phn tch d liu'
            ],
            posted: '5 ngy trc',
            urgent: false
        },
        {
            id: 3,
            title: 'Digital Marketing Specialist',
            department: 'marketing',
            location: 'H Ch Minh',
            type: 'Full-time',
            experience: '2-4 nm',
            salary: '12-18 triu',
            description: 'Pht trin v thc hin cc chin dch marketing s.',
            requirements: [
                'Kinh nghim vi Google Ads, Facebook Ads',
                'Thnh tho SEO/SEM',
                'K nng phn tch d liu',
                'Sng to v c t duy chin lc'
            ],
            posted: '1 tun trc',
            urgent: false
        },
        {
            id: 4,
            title: 'Backend Developer',
            department: 'tech',
            location: 'Remote',
            type: 'Full-time',
            experience: '2-4 nm',
            salary: '12-20 triu',
            description: 'Pht trin API v h thng backend cho cc ng dng.',
            requirements: [
                'Thnh tho Node.js, Python hoc Java',
                'Kinh nghim vi database (MySQL, MongoDB)',
                'Hiu bit v microservices',
                'K nng DevOps c bn'
            ],
            posted: '3 ngy trc',
            urgent: true
        },
        {
            id: 5,
            title: 'HR Business Partner',
            department: 'hr',
            location: 'H Ni',
            type: 'Full-time',
            experience: '3-5 nm',
            salary: '15-22 triu',
            description: 'H tr cc phng ban trong vic qun l nhn s.',
            requirements: [
                'Kinh nghim trong lnh vc HR',
                'K nng giao tip v thuyt phc',
                'Hiu bit v lut lao ng',
                'Kh nng lm vic a nhim'
            ],
            posted: '1 tun trc',
            urgent: false
        },
        {
            id: 6,
            title: 'Financial Analyst',
            department: 'finance',
            location: 'H Ch Minh',
            type: 'Full-time',
            experience: '2-4 nm',
            salary: '13-19 triu',
            description: 'Phn tch ti chnh v h tr ra quyt nh kinh doanh.',
            requirements: [
                'Bng cp v Ti chnh hoc K ton',
                'Thnh tho Excel v cc cng c phn tch',
                'K nng phn tch d liu',
                'Ch  n chi tit'
            ],
            posted: '4 ngy trc',
            urgent: false
        }
    ];

    const filteredJobs = selectedDepartment === 'all' 
        ? jobOpenings 
        : jobOpenings.filter(job => job.department === selectedDepartment);

    const benefits = [
        { icon: '', title: 'Lng Cnh Tranh', desc: 'Mc lng hp dn theo nng lc' },
        { icon: '', title: 'Bo Him y ', desc: 'Bo him sc khe v x hi' },
        { icon: '', title: 'o To', desc: 'C hi pht trin k nng lin tc' },
        { icon: '', title: 'Ngh Php', desc: '12 ngy php nm + ngh l' },
        { icon: '', title: 'Cng Tin', desc: 'Ba tra min ph ti cng ty' },
        { icon: '', title: 'Xe a n', desc: 'H tr i li cho nhn vin' }
    ];

    return (
        <div className="careers-page">
            <Header />
            
            {/* Hero Section */}
            <section className="careers-hero">
                <div className="container">
                    <div className="hero-content">
                        <h1 className="hero-title">C Hi Ngh Nghip</h1>
                        <p className="hero-subtitle">
                            Tham gia i ng Cellphones v cng chng ti xy dng tng lai cng ngh
                        </p>
                        <div className="hero-stats">
                            <div className="stat">
                                <span className="stat-number">200+</span>
                                <span className="stat-label">Nhn Vin</span>
                            </div>
                            <div className="stat">
                                <span className="stat-number">15+</span>
                                <span className="stat-label">Phng Ban</span>
                            </div>
                            <div className="stat">
                                <span className="stat-number">50+</span>
                                <span className="stat-label">Ca Hng</span>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            {/* Benefits Section */}
            <section className="benefits-section">
                <div className="container">
                    <h2 className="section-title">Quyn Li Nhn Vin</h2>
                    <div className="benefits-grid">
                        {benefits.map((benefit, index) => (
                            <div key={index} className="benefit-card">
                                <div className="benefit-icon">{benefit.icon}</div>
                                <h3>{benefit.title}</h3>
                                <p>{benefit.desc}</p>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Job Openings */}
            <section className="job-openings">
                <div className="container">
                    <div className="jobs-header">
                        <h2 className="section-title">V Tr Tuyn Dng</h2>
                        
                        {/* Department Filter */}
                        <div className="department-filter">
                            {departments.map(dept => (
                                <button
                                    key={dept.id}
                                    className={`filter-btn ${selectedDepartment === dept.id ? 'active' : ''}`}
                                    onClick={() => setSelectedDepartment(dept.id)}
                                >
                                    {dept.name}
                                </button>
                            ))}
                        </div>
                    </div>

                    <div className="jobs-grid">
                        {filteredJobs.map(job => (
                            <div key={job.id} className="job-card">
                                {job.urgent && <div className="urgent-badge">Khn Cp</div>}
                                
                                <div className="job-header">
                                    <h3 className="job-title">{job.title}</h3>
                                    <div className="job-meta">
                                        <span className="department">{departments.find(d => d.id === job.department)?.name}</span>
                                        <span className="location"> {job.location}</span>
                                        <span className="type">{job.type}</span>
                                    </div>
                                </div>

                                <div className="job-details">
                                    <div className="detail-item">
                                        <span className="label">Kinh nghim:</span>
                                        <span className="value">{job.experience}</span>
                                    </div>
                                    <div className="detail-item">
                                        <span className="label">Mc lng:</span>
                                        <span className="value salary">{job.salary}</span>
                                    </div>
                                    <div className="detail-item">
                                        <span className="label">ng:</span>
                                        <span className="value">{job.posted}</span>
                                    </div>
                                </div>

                                <div className="job-description">
                                    <p>{job.description}</p>
                                </div>

                                <div className="job-requirements">
                                    <h4>Yu cu:</h4>
                                    <ul>
                                        {job.requirements.map((req, index) => (
                                            <li key={index}>{req}</li>
                                        ))}
                                    </ul>
                                </div>

                                <div className="job-actions">
                                    <button className="apply-btn">ng Tuyn Ngay</button>
                                    <button className="save-btn">Lu Vic Lm</button>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Company Culture */}
            <section className="company-culture">
                <div className="container">
                    <h2 className="section-title">Vn Ha Cng Ty</h2>
                    <div className="culture-grid">
                        <div className="culture-item">
                            <div className="culture-icon"></div>
                            <h3>Hp Tc</h3>
                            <p>Lm vic nhm hiu qu v h tr ln nhau</p>
                        </div>
                        <div className="culture-item">
                            <div className="culture-icon"></div>
                            <h3>Sng To</h3>
                            <p>Khuyn khch  tng mi v i mi</p>
                        </div>
                        <div className="culture-item">
                            <div className="culture-icon"></div>
                            <h3>Pht Trin</h3>
                            <p>C hi thng tin v hc hi lin tc</p>
                        </div>
                        <div className="culture-item">
                            <div className="culture-icon"></div>
                            <h3>Mc Tiu</h3>
                            <p>Hng n thnh cng v cht lng</p>
                        </div>
                    </div>
                </div>
            </section>

            {/* Application Process */}
            <section className="application-process">
                <div className="container">
                    <h2 className="section-title">Quy Trnh Tuyn Dng</h2>
                    <div className="process-steps">
                        <div className="step">
                            <div className="step-number">1</div>
                            <h3>Np H S</h3>
                            <p>Gi CV v n ng tuyn qua website</p>
                        </div>
                        <div className="step">
                            <div className="step-number">2</div>
                            <h3>Sng Lc</h3>
                            <p>HR s xem xt v lin h trong 3-5 ngy</p>
                        </div>
                        <div className="step">
                            <div className="step-number">3</div>
                            <h3>Phng Vn</h3>
                            <p>Phng vn vi HR v trng phng</p>
                        </div>
                        <div className="step">
                            <div className="step-number">4</div>
                            <h3>Kt Qu</h3>
                            <p>Thng bo kt qu v onboarding</p>
                        </div>
                    </div>
                </div>
            </section>

            <Footer />
        </div>
    );
}

export default CareersPage;
