import React, { useState } from 'react';
import './ARGuide.css';

const ARGuide = ({ isOpen, onClose }) => {
    const [activeTab, setActiveTab] = useState('ar');

    if (!isOpen) return null;

    return (
        <div className="ar-guide-overlay">
            <div className="ar-guide-modal">
                <div className="ar-guide-header">
                    <h2> Hng dn s dng AR/VR</h2>
                    <button className="close-btn" onClick={onClose}></button>
                </div>
                
                <div className="ar-guide-tabs">
                    <button 
                        className={`tab-btn ${activeTab === 'ar' ? 'active' : ''}`}
                        onClick={() => setActiveTab('ar')}
                    >
                         AR Mode
                    </button>
                    <button 
                        className={`tab-btn ${activeTab === 'vr' ? 'active' : ''}`}
                        onClick={() => setActiveTab('vr')}
                    >
                         VR Mode
                    </button>
                    <button 
                        className={`tab-btn ${activeTab === 'troubleshoot' ? 'active' : ''}`}
                        onClick={() => setActiveTab('troubleshoot')}
                    >
                         X l li
                    </button>
                </div>

                <div className="ar-guide-content">
                    {activeTab === 'ar' && (
                        <div className="guide-section">
                            <h3> Chun b cho AR</h3>
                            <div className="guide-steps">
                                <div className="step">
                                    <div className="step-number">1</div>
                                    <div className="step-content">
                                        <h4>Ti marker Hiro</h4>
                                        <p>In marker trn giy A4 hoc hin th trn mn hnh khc</p>
                                        <a href="/hiro-marker.html" target="_blank" className="download-link">
                                             Ti marker
                                        </a>
                                    </div>
                                </div>
                                
                                <div className="step">
                                    <div className="step-number">2</div>
                                    <div className="step-content">
                                        <h4>Chun b mi trng</h4>
                                        <ul>
                                            <li>m bo nh sng  (khng qu ti hoc qu sng)</li>
                                            <li>Marker phi phng, khng b un cong</li>
                                            <li>Camera sch, khng b m</li>
                                        </ul>
                                    </div>
                                </div>
                                
                                <div className="step">
                                    <div className="step-number">3</div>
                                    <div className="step-content">
                                        <h4>Bt u AR</h4>
                                        <ul>
                                            <li>Bm nt "Bt u AR"</li>
                                            <li>Cho php truy cp camera</li>
                                            <li>Hng camera vo marker</li>
                                            <li>Gi khong cch 15-30cm</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                            
                            <div className="ar-demo">
                                <img 
                                    src="https://raw.githubusercontent.com/AR-js-org/AR.js/master/data/images/hiro.png" 
                                    alt="Hiro Marker" 
                                    className="marker-preview"
                                />
                                <p>Marker Hiro - hng camera vo y</p>
                            </div>
                        </div>
                    )}

                    {activeTab === 'vr' && (
                        <div className="guide-section">
                            <h3> Tri nghim VR</h3>
                            <div className="guide-steps">
                                <div className="step">
                                    <div className="step-number">1</div>
                                    <div className="step-content">
                                        <h4>Khi ng VR</h4>
                                        <p>Bm nt "Bt u VR" - khng cn camera</p>
                                    </div>
                                </div>
                                
                                <div className="step">
                                    <div className="step-number">2</div>
                                    <div className="step-content">
                                        <h4>iu khin view</h4>
                                        <ul>
                                            <li><strong>Chut:</strong> Ko  xoay camera</li>
                                            <li><strong>Touch:</strong> Vut  di chuyn gc nhn</li>
                                            <li><strong>Nt iu khin:</strong> Chuyn i view modes</li>
                                        </ul>
                                    </div>
                                </div>
                                
                                <div className="step">
                                    <div className="step-number">3</div>
                                    <div className="step-content">
                                        <h4>3 ch  xem</h4>
                                        <ul>
                                            <li><strong> Exterior:</strong> B mt ngoi thit b</li>
                                            <li><strong> Interior:</strong> Linh kin bn trong</li>
                                            <li><strong> Exploded:</strong> Cc b phn tch ri</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )}

                    {activeTab === 'troubleshoot' && (
                        <div className="guide-section">
                            <h3> X l s c</h3>
                            <div className="troubleshoot-list">
                                <div className="issue">
                                    <h4> AR khng nhn din marker</h4>
                                    <ul>
                                        <li>Kim tra nh sng - khng qu ti/sng</li>
                                        <li>Lm sch camera in thoi</li>
                                        <li>m bo marker khng b che khut</li>
                                        <li>Th di chuyn marker gn/xa hn</li>
                                        <li>In marker trn giy trng, mc en</li>
                                    </ul>
                                </div>
                                
                                <div className="issue">
                                    <h4> Khng truy cp c camera</h4>
                                    <ul>
                                        <li>Kim tra quyn camera trong browser</li>
                                        <li>m bo khng c app khc ang dng camera</li>
                                        <li>Th lm mi trang (F5)</li>
                                        <li>Dng HTTPS thay v HTTP</li>
                                    </ul>
                                </div>
                                
                                <div className="issue">
                                    <h4> AR/VR chy chm</h4>
                                    <ul>
                                        <li>ng cc tab browser khc</li>
                                        <li>Kim tra RAM cn trng</li>
                                        <li>Restart browser</li>
                                        <li>S dng Chrome/Firefox mi nht</li>
                                    </ul>
                                </div>
                                
                                <div className="issue">
                                    <h4> Li trn mobile</h4>
                                    <ul>
                                        <li>Xoay mn hnh v dc</li>
                                        <li>m bo khng bt ch  tit kim pin</li>
                                        <li>Dng browser mc nh ca h thng</li>
                                        <li>Tng  sng mn hnh</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default ARGuide;
