import React, { useState } from 'react';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import './BlogPage.css';
import { blogPosts } from '../data/blogPosts';
import { useHistory } from 'react-router-dom';

function BlogPage() {
    const [selectedCategory, setSelectedCategory] = useState('all');
    const history = useHistory();

    const categories = [
        { id: 'all', name: 'Tt C' },
        { id: 'news', name: 'Tin Tc' },
        { id: 'reviews', name: 'nh Gi' },
        { id: 'tips', name: 'Mo Vt' },
        { id: 'tech', name: 'Cng Ngh' }
    ];

    const filteredPosts = selectedCategory === 'all' 
        ? blogPosts 
        : blogPosts.filter(post => post.category === selectedCategory);

    const featuredPosts = blogPosts.filter(post => post.featured);

    const openPost = (id) => history.push(`/blog/${id}`);

    return (
        <div className="blog-page">
            <Header />
            
            {/* Hero Section */}
            <section className="blog-hero">
                <div className="container">
                    <div className="hero-content">
                        <h1 className="hero-title">Blog Cng Ngh</h1>
                        <p className="hero-subtitle">
                            Cp nht tin tc, nh gi v mo vt v cng ngh mi nht
                        </p>
                    </div>
                </div>
            </section>

            {/* Featured Posts */}
            <section className="featured-posts">
                <div className="container">
                    <h2 className="section-title">Bi Vit Ni Bt</h2>
                    <div className="featured-grid">
                        {featuredPosts.map(post => (
                            <article key={post.id} className="featured-card" onClick={() => openPost(post.id)} style={{cursor:'pointer'}}>
                                <div className="card-image">
                                    <img src={post.image} alt={post.title} />
                                    <div className="card-badge">Ni Bt</div>
                                </div>
                                <div className="card-content">
                                    <div className="card-meta">
                                        <span className="category">{categories.find(c => c.id === post.category)?.name}</span>
                                        <span className="date">{post.date}</span>
                                        <span className="read-time">{post.readTime}</span>
                                    </div>
                                    <h3 className="card-title">{post.title}</h3>
                                    <p className="card-excerpt">{post.excerpt}</p>
                                    <div className="card-footer">
                                        <span className="author">Bi {post.author}</span>
                                        <button className="read-more" onClick={(e)=>{e.stopPropagation();openPost(post.id)}}>c Thm</button>
                                    </div>
                                </div>
                            </article>
                        ))}
                    </div>
                </div>
            </section>

            {/* Blog Posts */}
            <section className="blog-posts">
                <div className="container">
                    <div className="posts-header">
                        <h2 className="section-title">Tt C Bi Vit</h2>
                        
                        {/* Category Filter */}
                        <div className="category-filter">
                            {categories.map(category => (
                                <button
                                    key={category.id}
                                    className={`filter-btn ${selectedCategory === category.id ? 'active' : ''}`}
                                    onClick={() => setSelectedCategory(category.id)}
                                >
                                    {category.name}
                                </button>
                            ))}
                        </div>
                    </div>

                    <div className="posts-grid">
                        {filteredPosts.map(post => (
                            <article key={post.id} className="post-card">
                                <div className="post-image">
                                    <img src={post.image} alt={post.title} />
                                    <div className="post-category">
                                        {categories.find(c => c.id === post.category)?.name}
                                    </div>
                                </div>
                                <div className="post-content">
                                    <div className="post-meta">
                                        <span className="post-date">{post.date}</span>
                                        <span className="post-read-time">{post.readTime}</span>
                                    </div>
                                    <h3 className="post-title">{post.title}</h3>
                                    <p className="post-excerpt">{post.excerpt}</p>
                                    <div className="post-footer">
                                        <span className="post-author">Bi {post.author}</span>
                                        <button className="post-read-more" onClick={() => openPost(post.id)}>c Thm</button>
                                    </div>
                                </div>
                            </article>
                        ))}
                    </div>
                </div>
            </section>

            {/* Newsletter */}
            <section className="newsletter">
                <div className="container">
                    <div className="newsletter-content">
                        <h2>ng K Nhn Tin</h2>
                        <p>Nhn thng tin v sn phm mi v bi vit hay nht</p>
                        <div className="newsletter-form">
                            <input type="email" placeholder="Nhp email ca bn" />
                            <button>ng K</button>
                        </div>
                    </div>
                </div>
            </section>

            <Footer />
        </div>
    );
}

export default BlogPage;
