import React from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import ARViewer from '../components/ARViewer/ARViewer';
import { mockProductsWithAR } from '../data/mockARProducts';
import './ARPage.css';

const ARPage = () => {
    // Use mock products with AR capabilities
    const sampleProducts = mockProductsWithAR;

    const [selectedProduct, setSelectedProduct] = React.useState(sampleProducts[0]);

    return (
        <div className="ar-page">
            <Header />

            <div className="ar-page-header">
                <div className="container" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', gap: '1rem' }}>
                    <div>
                        <h1> Tri nghim AR/VR</h1>
                        <p>Khm ph sn phm vi cng ngh thc t tng cng v thc t o</p>
                    </div>
                    <div style={{ display: 'grid', gap: 8, gridAutoFlow: 'column' }}>
                        <Link to="/ar-model-viewer" className="btn btn-primary">AR nhanh (model-viewer)</Link>
                        <Link to="/ar-webxr" className="btn btn-primary">Th WebXR (mi)</Link>
                        <Link to="/" className="btn btn-secondary"> V trang ch</Link>
                    </div>
                </div>
            </div>

            {/* Product Selection */}
            <div className="product-selection">
                <div className="container">
                    <h2>Chn sn phm  tri nghim:</h2>
                    <div className="product-cards">
                        {sampleProducts.map(product => (
                            <div 
                                key={product.id}
                                className={`product-card ${selectedProduct.id === product.id ? 'active' : ''}`}
                                onClick={() => setSelectedProduct(product)}
                            >
                                <img src={product.image} alt={product.name} />
                                <h3>{product.name}</h3>
                                <p>{product.price.toLocaleString('vi-VN')}</p>
                                {product.modelUrl && (
                                    <span className="model-badge"> 3D Model</span>
                                )}
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            {/* AR/VR Viewer */}
            <div className="ar-viewer-section">
                <div className="container">
                    <ARViewer product={selectedProduct} />
                </div>
            </div>

            {/* Instructions */}
            <div className="instructions-section">
                <div className="container">
                    <div className="instruction-grid">
                        <div className="instruction-card">
                            <h3> AR Mode</h3>
                            <ul>
                                <li>Cn c camera</li>
                                <li>In marker Hiro</li>
                                <li>Hng camera vo marker</li>
                                <li>Xem model 3D xut hin</li>
                            </ul>
                        </div>
                        <div className="instruction-card">
                            <h3> VR Mode</h3>
                            <ul>
                                <li>Khng cn camera</li>
                                <li>Xem model 3D trong khng gian o</li>
                                <li>Di chuyn chut  xoay view</li>
                                <li>Chuyn i cc ch  xem</li>
                            </ul>
                        </div>
                        <div className="instruction-card">
                            <h3> View Modes</h3>
                            <ul>
                                <li><strong>Exterior:</strong> Bn ngoi thit b</li>
                                <li><strong>Interior:</strong> Linh kin bn trong</li>
                                <li><strong>Exploded:</strong> Cc b phn tch ri</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>

            <Footer />
        </div>
    );
};

export default ARPage;
