import React, { useState, useEffect, useRef } from 'react';
import './GlobalAIChatbot.css';

function GlobalAIChatbot() {
    const [isOpen, setIsOpen] = useState(false);
    const [messages, setMessages] = useState([
        {
            id: 1,
            text: "Xin cho! Ti l AI Assistant ca Cellphones. Ti c th gip bn:\n\n Tr li mi cu hi\n T vn sn phm in thoi\n So snh gi c\n Tm kim thng tin\n a ra gi  mua sm\n\nBn mun hi g?",
            sender: 'bot',
            timestamp: new Date()
        }
    ]);
    const [inputText, setInputText] = useState('');
    const [isTyping, setIsTyping] = useState(false);
    const messagesEndRef = useRef(null);

    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    };

    useEffect(() => {
        scrollToBottom();
    }, [messages]);

    // Enhanced AI response vi OpenAI/Gemini integration
    const getAIResponse = async (userMessage) => {
        setIsTyping(true);
        
        try {
            // Call backend AI API
            const response = await fetch('http://localhost:4000/genai/chat', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    prompt: userMessage,
                    temperature: 0.7,
                    maxOutputTokens: 1024
                })
            });

            if (!response.ok) {
                throw new Error('API call failed');
            }

            const data = await response.json();
            return data?.data?.text || data?.data?.raw?.text ||
                (data?.data?.raw?.candidates && data.data.raw.candidates.map(c=>c.output||'').join('\n')) ||
                'Xin li, ti cha nhn c phn hi t AI.';
            
        } catch (error) {
            console.error('AI API Error:', error);
            
            // Fallback to enhanced local responses
            return getEnhancedLocalResponse(userMessage);
        } finally {
            setIsTyping(false);
        }
    };

    // Enhanced local responses (fallback)
    const getEnhancedLocalResponse = (userMessage) => {
        const lowerMessage = userMessage.toLowerCase();
        
        // General knowledge questions
        if (lowerMessage.includes('thi tit') || lowerMessage.includes('weather')) {
            return ` Ti khng th cung cp thng tin thi tit thi gian thc, nhng bn c th:\n\n Kim tra ng dng thi tit trn in thoi\n Hi Google Assistant hoc Siri\n Truy cp trang web thi tit\n\nTi c th gip bn tm in thoi c ng dng thi tit tt! `;
        }
        
        if (lowerMessage.includes('gi') || lowerMessage.includes('time')) {
            const now = new Date();
            return ` Hin ti l ${now.toLocaleTimeString('vi-VN')} ngy ${now.toLocaleDateString('vi-VN')}\n\nBn c cn in thoi c ng h p khng? Ti c th gi ! `;
        }
        
        if (lowerMessage.includes('hc') || lowerMessage.includes('study') || lowerMessage.includes('hc tp')) {
            return ` Ti c th gip bn tm in thoi ph hp cho vic hc tp:\n\n **in thoi hc tp tt:**\n iPhone SE - Compact, hiu nng tt\n Samsung Galaxy A54 - Mn hnh ln, pin tt\n Xiaomi Redmi Note 12 - Gi r, y  tnh nng\n\n **Tnh nng hu ch:**\n Mn hnh ln  c sch\n Pin tt  hc c ngy\n Camera tt  chp bi ging\n B nh ln  lu ti liu\n\nBn hc ngnh g? Ti s gi  ph hp! `;
        }
        
        if (lowerMessage.includes('lm vic') || lowerMessage.includes('work') || lowerMessage.includes('cng vic')) {
            return `  lm vic hiu qu, bn cn in thoi c:\n\n **in thoi lm vic tt:**\n iPhone 15 Pro - Hiu nng cao, camera chuyn nghip\n Samsung Galaxy S23 Ultra - S Pen, a nhim tt\n Google Pixel 8 Pro - AI tch hp, camera xut sc\n\n **Tnh nng quan trng:**\n Email, calendar tch hp\n Video call cht lng cao\n Chp nh ti liu r nt\n Pin c ngy\n Bo mt cao\n\nBn lm ngh g? Ti s t vn c th! `;
        }
        
        if (lowerMessage.includes('chi game') || lowerMessage.includes('gaming')) {
            return ` in thoi gaming tt nht:\n\n **Top Gaming Phones:**\n iPhone 15 Pro Max - Chip A17 Pro mnh nht\n Samsung Galaxy S23 Ultra - Snapdragon 8 Gen 2\n ASUS ROG Phone 7 - Chuyn gaming\n Xiaomi Black Shark 5 Pro - Gi tt\n\n **Tnh nng gaming:**\n Chip x l mnh\n RAM ln (8GB+)\n Mn hnh 120Hz+\n Pin ln\n Tn nhit tt\n m thanh stereo\n\nBn chi game g? PUBG, Mobile Legends, hay game khc? `;
        }
        
        if (lowerMessage.includes('chp nh') || lowerMessage.includes('camera') || lowerMessage.includes('photo')) {
            return ` in thoi camera tt nht:\n\n **Top Camera Phones:**\n iPhone 15 Pro Max - Camera 48MP, zoom 5x\n Samsung Galaxy S23 Ultra - Camera 200MP\n Google Pixel 8 Pro - AI Magic Eraser\n Xiaomi 13 Ultra - Leica camera\n\n **Tnh nng camera:**\n Chp m xut sc\n Zoom quang hc\n Video 4K/8K\n Ch  chuyn nghip\n AI scene detection\n\nBn thch chp g? Chn dung, phong cnh, hay macro? `;
        }
        
        if (lowerMessage.includes('pin') || lowerMessage.includes('battery')) {
            return ` in thoi pin tt nht:\n\n **Top Battery Life:**\n Samsung Galaxy S23 Ultra - 5000mAh\n iPhone 15 Plus - Pin c ngy\n Xiaomi Redmi Note 12 - 5000mAh gi r\n OnePlus 11 - Sc nhanh 100W\n\n **Tnh nng pin:**\n Dung lng ln (4000mAh+)\n Sc nhanh (30W+)\n Sc khng dy\n Tit kim in\n Reverse charging\n\nBn cn pin dng c bao lu? 1 ngy hay nhiu hn? `;
        }
        
        if (lowerMessage.includes('gi r') || lowerMessage.includes('budget') || lowerMessage.includes('tit kim')) {
            return ` in thoi gi r tt nht:\n\n **Budget Phones:**\n Xiaomi Redmi Note 12 - 4.290.000\n Samsung Galaxy A14 - 3.990.000\n iPhone SE (2022) - 9.990.000\n Realme C55 - 3.490.000\n\n **Li khuyn:**\n Di 5tr: Xiaomi, Realme\n 5-10tr: Samsung A series\n 10-15tr: iPhone SE, Samsung S\n Trn 15tr: Flagship models\n\nBn c ngn sch bao nhiu? Ti s gi  ph hp! `;
        }
        
        if (lowerMessage.includes('so snh') || lowerMessage.includes('compare')) {
            return ` Ti c th so snh in thoi cho bn!\n\n **Hy cho ti bit:**\n 2-3 in thoi bn mun so snh\n Tiu ch quan trng (gi, camera, pin, hiu nng)\n Ngn sch ca bn\n\n **Ti s so snh:**\n Gi c\n Thng s k thut\n Camera\n Pin\n Hiu nng\n Tnh nng c bit\n\nV d: "So snh iPhone 15 Pro v Galaxy S23 Ultra" `;
        }
        
        if (lowerMessage.includes('khuyn mi') || lowerMessage.includes('sale') || lowerMessage.includes('gim gi')) {
            return ` Thng tin khuyn mi hin ti:\n\n **Khuyn mi ang din ra:**\n iPhone 15 series - Gim n 2 triu\n Samsung Galaxy S23 - Tng Galaxy Buds\n Xiaomi Redmi Note 12 - Gim 500k\n Accessories - Gim 30%\n\n **u i c bit:**\n Tr gp 0% li sut\n Tng ph kin\n Bo hnh m rng\n i c ly mi\n\nBn quan tm sn phm no? Ti s kim tra khuyn mi c th! `;
        }
        
        // Default response for unknown questions
        return ` Ti hiu bn ang hi v "${userMessage}". 

 ti c th h tr tt nht, bn c th hi v:

 **Sn phm:** iPhone, Samsung, Xiaomi, Google Pixel...
 **Gi c:** Di 5tr, 5-10tr, 10-20tr, trn 20tr...
 **Tnh nng:** Camera, pin, hiu nng, gaming...
 **So snh:** Gia cc sn phm
 **Mc ch:** Hc tp, lm vic, chp nh, gaming...
 **Khuyn mi:** Sale, gim gi, tr gp...

Hoc bn c th hi bt k cu hi no khc! Ti s c gng tr li tt nht c th! `;
    };

    const handleSendMessage = async () => {
        if (!inputText.trim()) return;

        const userMessage = {
            id: Date.now(),
            text: inputText,
            sender: 'user',
            timestamp: new Date()
        };

        setMessages(prev => [...prev, userMessage]);
        setInputText('');

        const botResponse = await getAIResponse(inputText);
        
        const botMessage = {
            id: Date.now() + 1,
            text: botResponse,
            sender: 'bot',
            timestamp: new Date()
        };

        setMessages(prev => [...prev, botMessage]);
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter' && !e.shiftKey) {
            e.preventDefault();
            handleSendMessage();
        }
    };

    const quickQuestions = [
        "in thoi no tt nht?",
        "So snh iPhone v Samsung",
        "in thoi no pin tt?",
        "Camera no chp m tt?",
        "in thoi gaming tt nht?",
        "Khuyn mi g ang c?"
    ];

    return (
        <div className={`global-ai-chatbot ${isOpen ? 'open' : ''}`}>
            {/* Chat Button */}
            <button 
                className="chat-toggle"
                onClick={() => setIsOpen(!isOpen)}
            >
                <span className="chat-icon"></span>
                <span className="chat-text">AI Assistant</span>
                <span className="chat-badge">NEW</span>
            </button>

            {/* Chat Window */}
            <div className="chat-window">
                <div className="chat-header">
                    <div className="bot-info">
                        <div className="bot-avatar"></div>
                        <div className="bot-details">
                            <h3>AI Assistant</h3>
                            <span className="status">Lun sn sng</span>
                        </div>
                    </div>
                    <button 
                        className="close-btn"
                        onClick={() => setIsOpen(false)}
                    >
                        
                    </button>
                </div>

                <div className="chat-messages">
                    {messages.map((message) => (
                        <div 
                            key={message.id} 
                            className={`message ${message.sender}`}
                        >
                            <div className="message-content">
                                <div className="message-text">
                                    {message.text.split('\n').map((line, index) => (
                                        <div key={index}>
                                            {line}
                                            {index < message.text.split('\n').length - 1 && <br />}
                                        </div>
                                    ))}
                                </div>
                                <div className="message-time">
                                    {message.timestamp.toLocaleTimeString('vi-VN', { 
                                        hour: '2-digit', 
                                        minute: '2-digit' 
                                    })}
                                </div>
                            </div>
                        </div>
                    ))}
                    
                    {isTyping && (
                        <div className="message bot">
                            <div className="message-content">
                                <div className="typing-indicator">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </div>
                        </div>
                    )}
                    
                    <div ref={messagesEndRef} />
                </div>

                {/* Quick Questions */}
                {messages.length === 1 && (
                    <div className="quick-questions">
                        <h4>Cu hi thng gp:</h4>
                        <div className="question-buttons">
                            {quickQuestions.map((question, index) => (
                                <button 
                                    key={index}
                                    className="question-btn"
                                    onClick={() => setInputText(question)}
                                >
                                    {question}
                                </button>
                            ))}
                        </div>
                    </div>
                )}

                <div className="chat-input">
                    <input
                        type="text"
                        value={inputText}
                        onChange={(e) => setInputText(e.target.value)}
                        onKeyPress={handleKeyPress}
                        placeholder="Hi ti bt k iu g..."
                        className="message-input"
                    />
                    <button 
                        onClick={handleSendMessage}
                        className="send-btn"
                        disabled={!inputText.trim()}
                    >
                        <span></span>
                    </button>
                </div>
            </div>
        </div>
    );
}

export default GlobalAIChatbot;
